
footer {
    background-color: #2D2B2F;
    padding-top: 50px;


}

footer > div {
    display: flex;
    flex-direction: row;

    justify-content: space-around;
    margin: 0 auto 50px;
}

.contact-us > h3, .subscribe > h3 {
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    font-style: normal;
}

.contact-us-item {
    display: flex;
    flex-direction: row;

    margin-bottom: 27px;
    cursor: pointer;
}

.contact-us-item > img {
    margin-right: 40px;
    width: 30px;
    height: 30px;
}

.contact-us-item:last-child > img {
    width: 25px;
    height: 30px;
    margin-right: 40px;
}


.contact-us-item > div {
    color: #E5E5E5;
    font-size: 24px;
    font-weight: 400;
    max-width: 200px;
    text-align: left;
}

.subscribe {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.footer-contacts h3 {
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    font-style: normal;
}

.subscribe-form label {
    color: #E5E5E5;
    font-size: 24px;
    font-weight: 400;
}

.form-container {
    margin-top: 10px;
}

#subscribe-email {
    width: 447px;
    padding: 17px 21px;
    font-size: 24px;
    margin-right: 30px;
    border-radius: 3px;
}

.subscribe-form-btn {
    width: 169px;
    color: #E5E5E5;
    font-size: 24px;
    text-transform: uppercase;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 3px;
    padding: 17px 11px;
    box-sizing: border-box;
    cursor: pointer;
}

.subscribe-form-btn:hover {
    background-color: #FFCA62;
    color: #2D2B2F;
    border-color: #FFCA62;
}

.social-list {
    width: 424px;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.social-link {
    display: block;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
}

.social-link-youtube {
    background-image: url("../img/youtube.png");
}

.social-link-youtube:hover {
    background-image: url("../img/youtube-active.png");
}

.social-link-linkedin {
    background-image: url("../img/in.png");
}

.social-link-linkedin:hover {
    background-image: url("../img/in-active.png");
}

.social-link-google {
    background-image: url("../img/google.png");
    width: 48px;
}

.social-link-google:hover {
    background-image: url("../img/google-active.png");
    width: 48px;
}

.social-link-facebook {
    background-image: url("../img/fb.png");
}

.social-link-facebook:hover {
    background-image: url("../img/fb-active.png");
}

.social-link-twitter {
    background-image: url("../img/tw.png");
}

.social-link-twitter:hover {
    background-image: url("../img/tw-active.png");
}
