/*GREENS: #4ac4ac, #399988, #17694f, #0a1a17;*/

.slider {
    pointer-events: none;
    margin: 0px;
    cursor: pointer;
    /*width:200px;*/

    padding-left: 30%;
    padding-right: 30%;
    /*height:6px;*/

    background-color: #17694f;
    box-sizing: border-box;
}

.slider.focusable {
    border: 1px solid #222
}

.thumb {
    cursor: pointer;
    border-color: #333;
    border-style: solid;
    /*border-width:4px; width:16px; height:16px;*/

    background-color: #0f4534;
    background-color: rgba(100, 100, 100, 1);
    border-radius: 50%;
    position: absolute;
    /*top:-6px;left:50px;*/
}

.track {
    pointer-events: none;
    height: 100%;
    background-color: #4ac4ac;
}

.container {
    position: relative;
    margin: 200px auto;
    height: 30px;
    width: 220px;
}

.output {
    pointer-events: none;
    margin: 0;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50% 50% 0 50%;
    background-color: #4ac4ac;
    text-align: center;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    top: -45px;
    /*left:45px*/;
}

.output p {
    pointer-events: none;
    font-size: 14px;
    color: #0a1a17;
    text-align: center;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
