.successful-order-page {
    width: 1404px;
    margin: 0 auto;
    background: #FFFFFF;
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    box-shadow: 0px 2px 2px rgb(0 0 0 / 25%);
    font-style: normal;
    font-weight: 400;
    color: #3E3C41;
}

.successful-order-page_header {
    padding: 60px 80px 40px 82px;
    font-weight: 700;
    font-size: 36px;
    display: flex;
    justify-content: space-between;
    color: #3E3C41;
}

.successful-order-page_header .ticket-price {
    align-items: center;
}

.ticket-price {
    display: flex;
    font-family: inherit;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: #fff;
}

.successful-order-page_header .ticket-price span {
    color: #928F94;
    font-weight: 400;
    font-size: 36px;
    margin-right: 20px;
}

.successful-order-page_header .ticket-price .summary-value {
    font-weight: 700;
    font-size: 36px;
    color: #3E3C41;
}

.successful-order-page_header .value-image {
    width: 21px;
    height: 27px;
    margin-left: 10px;
}

.successful-order-page_action {
    background-color: #F4F3F6;
    border: 1px solid #928F94;
    transform: rotate(0.04deg);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    padding: 40px 250px;
}

.action-plan_item {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    width: 155px;
    text-align: center;
    font-weight: normal;
    font-size: 18px;
}

.action-plan_item-image.email {
    background-image: url(data:image/png;base64,);
}

.action-plan_item-image {
    width: 155px;
    height: 155px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 155px;
    margin-bottom: 11px;
}

.action-plan_item-image.print {
    background-image: url(data:image/png;base64,);
}

.action-plan_item-image {
    width: 155px;
    height: 155px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 155px;
    margin-bottom: 11px;
}

.action-plan_item-image.control {
    background-image: url(data:image/png;base64,);
}

.action-plan_item-image {
    width: 155px;
    height: 155px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 155px;
    margin-bottom: 11px;
}

.successful-order-page_text {
    display: flex;
    flex-direction: column;
    padding: 95px 111px 130px 205px;
    align-items: start;
}


.successful-order-page_text .appeal {
    font-weight: 700;
    font-size: 36px;
    margin-bottom: 50px;
}

.successful-order-page_text span {
    display: block;
    color: #292929;
    font-weight: 400;
    font-size: 24px;
}

.successful-order-page_rating {
    background-color: #FFA800E0;
    padding: 50px 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
    font-weight: 400;
    color: #292929;
}

.rating-wrapper {
    display: flex;
    align-items: center;
}

.rating-star-list {
    display: flex;
    margin-left: 30px;
}

.rating-star-item {
    width: 48px;
    height: 48px;
    margin-right: 20px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 48px;
    cursor: pointer;
}

.successful-order-page_rating .redirect-mainPage-btn {
    width: 343px;
    box-sizing: border-box;
    background-color: #FFA800E0;
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    color: #292929;
    padding: 16px 11px;
    border: 1px solid #3E3C41;
    border-radius: 5px;
}

.rating-star-item.active, .rating-star-item:hover {
    background-image: url(data:image/png;base64,);
}

.header-main {
    display: flex;
    justify-content: center;
    margin-top: 250px;
    margin-bottom: 250px;
}

.header-title {
    color: #fff;
    font-size: 72px;
    font-weight: 200;
    line-height: 84px;
    display: flex;
    flex-direction: column;
    margin-right: 105px;
    margin-left: 120px;
    align-self: center;
}

.successful-order-page_text span:last-of-type {
    margin-top: 35px;
    font-weight: 700;
}
.successful-order-page_text span {
    display: block;
    color: #292929;
    font-weight: 400;
    font-size: 24px;
}
